function checkTxt(text) {
	return new Promise((resolve, reject) => {
		uni.showLoading({
			title: '...',
			mask: true
		});
		wx.cloud.callFunction({
			name: 'Check',
			data: {
				txt: text
			},
			success: (res) => {
				uni.hideLoading();
				if (res.result.errCode == 87014) {
					wx.showToast({
						title: ',',
						icon: 'none'
					});
					reject();
				} else {
					resolve(true);
				}
			},
			fail(e) {
				uni.hideLoading();
				wx.showToast({
					title: '',
					icon: 'none'
				});
			}
		});
	});
};

function checkImg1(index, path, lists) {
	return new Promise((resolve, reject) => {
		let encode = ''
		let buffer = uni.getFileSystemManager().readFileSync(path, encode);
		imgSecCheck({
				contentType: 'image/jpeg',
				buffer: buffer,
				encode: encode
			}).then(res => {
				resolve(true)
			})
			.catch(err => {
				wx.showToast({
					title: '',
					icon: 'none'
				});
				lists.splice(index, 1);
				reject(false)
			})
	})
}

function imgSecCheck(media) {
	return new Promise((resolve, reject) => {
		wx.cloud.callFunction({
			name: 'imgCheck',
			data: {
				media: media
			},
			complete: ress => { //
				if (ress.result.code != 200) { //
					// resolve(false)
					reject(false)
				} else {
					resolve(true)
				}
			}
		})
	})
}

function checkImg(index, path, lists) {
	// uni.showLoading({
	//     title: '...',
	//     mask: true
	// });
	return new Promise((resolve, reject) => {
		uni.getFileSystemManager().readFile({
			filePath: path,
			success: buffer => {
				wx.cloud.callFunction({
					name: 'Check',
					data: {
						value: buffer.data
					},
					success(res) {
						if (res.result.errCode == 87014) {
							wx.showToast({
								title: '',
								icon: 'none'
							});
							lists.splice(index, 1);
							reject(false);
						} else {
							resolve(true);
						}
					},
					fail(e) {
						wx.showToast({
							title: '',
							icon: 'none'
						});
						lists.splice(index, 1);
						reject(false);
					}
				});
			}
		});
	});
};
module.exports = {
	checkImg,
	checkTxt,
	checkImg1
}
